@import url(https://fonts.googleapis.com/css?family=Roboto:100italic,100,300italic,300,700italic,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

#navbar {
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background: rgba(12, 12, 12, 0.801);
  color: #fff;
  align-items: center;
  justify-content: space-evenly;
  z-index: 99999;
  padding: 1rem 0rem;
}

#navbar ul {
  display: flex;
  align-items: center;
  list-style: none;
}

#navbar ul li a {
  color: #fff;
  padding: 0.75rem;
  margin: 0 0.25rem;
  text-decoration: none;
}

#navbar ul li a:hover {
  background: #00b217;
  border-radius: 5px;
}
#navbar .logo {
  flex: 0.72 1;
}
.text-primary {
  color: #007bff;
}
#navbar li > .selected {
  background: #00b217;
  border-radius: 5px;
}
@media (max-width: 641px) {
  #navbar {
    flex-direction: column;
    align-items: center;
  }

  #navbar .logo {
    margin-bottom: 0.5rem;
    text-align: center;
  }

  #navbar ul {
    padding: 0.5rem;
  }
  #navbar ul li a {
    font-size: 0.75rem;
    color: #fff;
    padding: 0.63rem;
    margin: 0 0.12rem;
    text-decoration: none;
  }
}

:root {
  --active: #007bff;
  --recovered: #00b217;
  --deaths: #c80414;
}

.infoBox {
  /* take as much room as it can */
  flex: 1 1;
  cursor: pointer;
}
/* give all except the last child of parent */
.infoBox:not(:last-child) {
  margin-right: 15px;
}

.infoBox_cases {
  /* color:#cc1034; */
  font-weight: 600;
  font-size: 1.9rem;
  margin-bottom: 0.5rem;
}
.infoBox_total {
  font-weight: 700 !important;
  font-size: 1.2rem !important;
  /* margin-top: 15px !important; */
  display: grid !important;
}
.infoboxTotalspan {
  /* display:grid !important; */
  /* font-size:1.4em; */
}
.infoBox--green {
  border-top: 10px solid #00b217;
  background-color: transparent !important;
  /* background-color: rgba(0, 178, 24, 0.136) !important; */
}
.infoBox--green .infoTitle {
  color: #00b217;
  color: var(--recovered);
}
.infoBox--green .infoBox_cases {
  color: #00b217;
  color: var(--recovered);
}
.infoBox--green .infoBox_total {
  color: #00b217;
  color: var(--recovered);
}
.infoBox--red {
  border-top: 10px solid #c80414;
  background-color: transparent !important;
}
.infoBox--red .infoTitle {
  color: #c80414;
  color: var(--deaths);
}
.infoBox--red .infoBox_cases {
  color: #c80414;
  color: var(--deaths);
}
.infoBox--red .infoBox_total {
  color: #c80414;
  color: var(--deaths);
}
.infoBox--blue {
  border-top: 10px solid #007bff;
  background-color: transparent !important;
  /* background-color: rgba(0, 123, 255, 0.136) !important; */
}
.infoBox--blue .infoTitle {
  color: #007bff;
  color: var(--active);
}
.infoBox--blue .infoBox_cases {
  color: #007bff;
  color: var(--active);
}
.infoBox--blue .infoBox_total {
  color: #007bff;
  color: var(--active);
}

.isHighlighted--blue :hover {
  border-radius: 5px;
  cursor: pointer;
  background: rgba(0, 123, 255, 0.125) !important;
}
.isHighlighted--green :hover {
  border-radius: 5px;
  cursor: pointer;
  /* z-index: 10 !important;
    opacity: 1; */
  background-color: rgba(0, 178, 24, 0.136) !important;
}
.isHighlighted--red :hover {
  border-radius: 5px;
  cursor: pointer;
  background-color: rgba(200, 4, 20, 0.136) !important;
}
.isActive--blue {
  border-radius: 5px;
  cursor: pointer;
  background: rgba(0, 123, 255, 0.125) !important;
}
.isActive--green {
  border-radius: 5px;
  cursor: pointer;
  /* z-index: 10 !important;
    opacity: 1; */
  background-color: rgba(0, 178, 24, 0.136) !important;
}
.isActive--red {
  border-radius: 5px;
  cursor: pointer;
  background-color: rgba(200, 4, 20, 0.136) !important;
}

@media (max-width: 768px) {
  .infoBox {
    font-size: 0.8rem !important;
    text-align: justify;
  }
  .MuiTypography-h6 {
    font-size: 1rem !important;
    height: 60px !important;
    text-align: justify !important;
  }
  .infoBox_total {
    font-size: 0.8rem !important;
  }
}
@media (max-width: 500px) {
  .infoBox {
    font-size: 0.4rem !important;
    text-align: center;
    justify-content: center;

    margin: 0 !important;
  }
  .MuiTypography-h6 {
    font-size: 1rem !important;
    height: 60px !important;
  }
  .infoBox_total {
    font-size: 0.5rem !important;
  }
  .infoBox_cases {
    font-size: 1.25rem !important;
  }
  .infoTitle {
    font-size: 0.7rem !important;
  }
  .infoboxTotalspan {
    font-size: 0.5rem !important;
  }
}

.map {
  height: 500px;
  background-color: rgb(21, 34, 44);
  padding: 0.5rem;
  border-radius: 10px;

  box-shadow: 0 0 28px -6px rgba(0, 0, 0, 0.5);
}
/* leaflet-container comes with Map */
.map .leaflet-container {
  /* must set a height as it is 0 by default */
  height: 100%;
}
.info-container {
  width: 100%;
}
.info-flag {
  height: 80px;
  width: 100%;
  background-size: cover;
  border-radius: 8px;
}
.info-name {
  font-size: 20px;
  font-weight: bold;
}
.info-flag img {
  width: 100px;
  border-radius: 5px;
}
.info-confirmed,
.info-recovered,
.info-deaths {
  font-size: 16px;
  margin-top: 5px;
  font-family: sans-serif;
}
.leaflet-popup-content-wrapper {
  background-color: rgba(19, 28, 35, 0.863) !important;
  color: white !important;
}

table {
  justify-content: start;
  width: 100% !important;
  height: 31.5rem;
  border-collapse: collapse;
  border-spacing: 0;
  overflow: scroll;
}
table td {
  padding: 0.6rem;
}
table th {
  padding: 0.3rem;
  background-color: black !important;
  color: #ffffff;
}
table th,
td {
  text-align: center;
}
table tr:nth-of-type(odd) {
  color: white !important;
  background-color: transparent;
}
table tr:nth-of-type(even) {
  color: white !important;
  background-color: rgba(12, 19, 23, 0.979) !important;
}

.thead {
  align-items: center;
  justify-content: center;
  font-size: 0.9rem;
}
.MuiTableSortLabel-root.MuiTableSortLabel-active.MuiTableSortLabel-root.MuiTableSortLabel-active
  .MuiTableSortLabel-icon {
  font-size: inherit;
}
.MuiTableSortLabel-icon {
  opacity: 1 !important;
  color: #ffffff !important;
}
@media (max-width: 500px) {
  table,
  .thead {
    font-size: 0.6rem;
  }
  table {
    height: 100% !important;
  }
  .MuiTableSortLabel-icon {
    font-size: 0.7rem !important;
  }
  /* .MuiTableSortLabel-root.MuiTableSortLabel-active.MuiTableSortLabel-root.MuiTableSortLabel-active .MuiTableSortLabel-icon {
        font-size: 0.3rem !important;
    } */
  table td {
    padding: 0.4rem;
  }
}

