#navbar {
  display: flex;
  position: sticky;
  top: 0;
  background: rgba(12, 12, 12, 0.801);
  color: #fff;
  align-items: center;
  justify-content: space-evenly;
  z-index: 99999;
  padding: 1rem 0rem;
}

#navbar ul {
  display: flex;
  align-items: center;
  list-style: none;
}

#navbar ul li a {
  color: #fff;
  padding: 0.75rem;
  margin: 0 0.25rem;
  text-decoration: none;
}

#navbar ul li a:hover {
  background: #00b217;
  border-radius: 5px;
}
#navbar .logo {
  flex: 0.72;
}
.text-primary {
  color: #007bff;
}
#navbar li > .selected {
  background: #00b217;
  border-radius: 5px;
}
@media (max-width: 641px) {
  #navbar {
    flex-direction: column;
    align-items: center;
  }

  #navbar .logo {
    margin-bottom: 0.5rem;
    text-align: center;
  }

  #navbar ul {
    padding: 0.5rem;
  }
  #navbar ul li a {
    font-size: 0.75rem;
    color: #fff;
    padding: 0.63rem;
    margin: 0 0.12rem;
    text-decoration: none;
  }
}
